import React from 'react';
import contacts from "../data/contact.js"


const rows = contacts.reduce(function (rows, key, index) {
    return (index % 2 === 0 ? rows.push([key]) : rows[rows.length - 1].push(key)) && rows;
}, []);

console.log(rows)


const Socials = () => (


    <div style={{ marginLeft: "20%", paddingTop: "7%" }}>
        {rows.map((row) =>
            <li style={{ display: 'flex', maxWidth: "80%", maxHeight: "80%", marginLeft: "12%" }}>
                {row.map((item) =>
                    <div key={item.icon} style={{ display: 'flex', padding: "1%" }}>
                        <a href={item.link} style={{ textDecoration: "none" }}>
                            <div style={{ display: "flex" }}>
                                <img src={item.icon} alt={item.label} style={{ maxWidth: "15%", paddingTop: '5%' }} />
                            </div>
                        </a>
                    </div>
                )}
            </li>
        )}
    </div>


);

export default Socials;
